@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*
{
  box-sizing: border-box;
}

body
{
 
  color: white;
  font-family: 'Poppins', 'sans-serif';
  margin: 0;
}
.app
{
  background-color: #111;
}

.row
{
    margin-left: 20px;
}
.row_poster 
{
    object-fit: contain;
    width: 100%;
    max-height: 100px;
    transition: -webkit-transform 450ms;
    transition: transform 450ms;
    transition: transform 450ms, -webkit-transform 450ms;
    margin-right: 10px;
}
.row_poster:hover
{
    -webkit-transform:scale(1.08) ;
            transform:scale(1.08) ;
}
.row_posters
{
    display: -webkit-flex;
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll ;
    padding: 20px;
}
.row_posters::-webkit-scrollbar {
    display: none;
}
.row_posterLarge
{
    max-height: 250px;
}
.row_posterLarge:hover
{
    -webkit-transform: scale(1.09);
            transform: scale(1.09);
}
.banner
{
    color: white;
    object-fit: contain;
    height: 448px;
  
}
.banner_content
{
margin-left: 30px;
padding-top: 40px;
height: 190px;
}
.banner_title
{
    font-size: 3em;
    font-weight: 800;
    padding-bottom: 0.3em;
}

.banner_description
{
width: 45rem;
line-height: 1.3;
padding-top: 1rem;
font-size: 0.8rem;
max-height: 360px;
height: 80px;
}
.btn
{
    cursor: pointer;
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    background-color: rgba(56, 56, 56, 0.589);
    padding-bottom: 0.5rem;
}
.btn:hover
{
    color: black;
    background-color: white;
    transition: all 0.5s;

}
.banner_opacity
{
  height: 7.4rem;
   background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 1, 0.473),
    #111
  );
}

.nav
{
    position: fixed;
    top: 0;
    width: 100%;
    padding: 20px;
    height: 50px;

    display: -webkit-flex;

    display: flex;
  
}
.nav_logo
{
   position: fixed;
   left: 20px;
    width: 80px;
    object-fit: contain;
}
.nav_black
{
  background-color: black;
}
